
.ini video{
    width: 85%;
}
hr{
    margin-bottom: 5rem;
}
.cards{
    display: flex;
}
.cards h2{
    font-size: 2.5rem;
    font-weight: 400;
    color: rgb(70, 56, 56);
}
.card{
    display: inline-block;
    margin-top: 5%;
    width: 30%;
    padding: 1.5rem;
    margin: 1%;
    border-radius: 10px;
    border: 1px solid #C22D2D;
}
.card:hover{
    background-color: #c22d2d2a;
    padding: 2rem;
}
.card2{
    display: inline-block;
    margin-top: 5%;
    width: 30%;
    padding: 1.5rem;
    margin: 1%;
    border-radius: 10px;
    border: 1px solid #C22D2D;
}
.card2:hover{
    background-color: #c22d2d2a;
    padding: 2rem;
}
.img{
    display: inline-block;
    margin-top: 5%;
    width: 30%;
    padding: 1.5rem;
    margin: 1%;
}
.img:hover{
    width: 35%;
    padding: 0px;
    margin: 0px;
}
.img img{
    width: 100%;
}
@media (min-width:200px){
    hr{
        margin-bottom: 1rem;
    }
    .cards h2{
        font-size: 1.1rem;
    }
    .card{
        margin-top: 3%;
        width: 48%;
        height: 350px;
        padding: .3rem;
        margin: .3%;
        border-radius: 10px;
    }
    .mais{
        display: none;
    }
    .ma{
        display: block;
    }
    .card h3{
     font-size: 1rem;
    }
    .card p{
     font-size: .9rem;
    }
    .card2{
       display: none;
    }
    .card:hover{
        padding: .6rem;
    }
    .img{
        display: inline-block;
        margin-top: 5%;
        width: 31%;
        padding: .5rem;
        margin: .5%;
    }
    .img:hover{
        width: 35%;
        padding: 0px;
        margin: 0px;
    }
    .img img{
        width: 100%;
    }
}
@media (min-width:480px){
    hr{
        margin-bottom: 2rem;
    }

    .card{
        margin-top: 3%;
        width: 48%;
        height: 160px;
        padding: .3rem;
        margin: .3%;
        border-radius: 10px;
    }
    .mais{
        display: none;
    }
    .ma{
        display: block;
    }
    .card2{
       display: none;
    }
    .card:hover{
        padding: .6rem;
    }
  
}
@media (min-width:768px){
    hr{
        margin-bottom: 3rem;
    }
    .cards h3{
        font-size: 1.1rem;
    }
    .card{
        margin-top: 3%;
        width: 32%;
        height: 220px;
        padding: 1rem;
        margin: 1%;
        border-radius: 10px;
    }
    .mais{
        display: none;
    }
    .ma{
        display: block;
    }
    .card2{
       display: block;
       margin-top: 3%;
       width: 30%;
       height: 220px;
       padding: 1rem;
       margin: 1%;
       border-radius: 10px;
    }
    .card:hover{
        padding: 1.1rem;
    }
  
}
@media (min-width:1024px){
    hr{
        margin-bottom: 3rem;
    }
    .cards h3{
        font-size: 1.7rem;
    }
    .card{
        margin-top: 3%;
        width: 32%;
        height: 220px;
        padding: 1rem;
        margin: 1%;
        border-radius: 10px;
    }
    .mais{
        display: none;
    }
    .ma{
        display: block;
    }
    .card2{
       display: block;
       margin-top: 3%;
       width: 30%;
       height: 220px;
       padding: 1rem;
       margin: 1%;
       border-radius: 10px;
    }
    .card:hover{
        padding: 1.2rem;
    }
}