

.nav{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-around;
    align-items: center;
    background-color: #ffffff;
    padding: 2px;
}
.nav img{
    width: 20%;
}
.nav img:hover{
    width: 22%;
}
.div{
    background-color: #C22D2D;
    padding: 4px;
}
nav a{
    text-decoration: none;
    padding: 5px;
    margin-left: 15px;
    color: #fff;
    font-size: 1.3rem;
}
nav a:hover{
    color: #C22D2D;
    background-color: #fff;
    margin: 1px solid #C22D2D;
}
@media (min-width:200px){
    .div{
        padding: 2px;
    }
    nav a{
        padding: 3px;
        margin-left: 12px;
        color: #fff;
        font-size: 1rem;
    }
}
@media (min-width:480px){
    .div{
        padding: 3px;
    }
    nav a{
        padding: 3px;
        margin-left: 13px;
        color: #fff;
        font-size: 1.1rem;
    }
}
@media (min-width:768px){
    .div{
        padding: 4px;
    }
    nav a{
        padding: 4px;
        margin-left: 14px;
        color: #fff;
        font-size: 1.2rem;
    }
}
@media (min-width:1024px){
    .div{
        padding: 4px;
    }
    nav a{
        padding: 5px;
        margin-left: 15px;
        color: #fff;
        font-size: 1.3rem;
    } 
}
